body {
  background-color: #faebd7;
  font-family: Roboto, sans-serif;
}

.howard {
  width: auto;
  max-height: 800px;
  margin-top: 220px;
}

.cat-info {
  flex-direction: column;
  align-items: center;
  margin: 0 10%;
  padding: 0 5%;
  display: flex;
}

img {
  width: 75%;
  border: 1px solid;
  border-radius: 20px;
}

.breed-select {
  width: 300px;
  margin: 2% 15% 0;
  padding: 10px;
  font-size: 16px;
}

.container {
  height: 70vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 60px;
  height: 60px;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: rotation 1s linear infinite;
  display: flex;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 44px;
  height: 44px;
  background: #faebd7;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

